<!--  -->
<template>
	<div>
		<div class="userBox">
			<div class="userImg"><img :src="detailInfo.headimg"></div>
			<div class="userFont">
				<p class="userName">{{detailInfo.vipName}}</p>
				<p class="userTitle">{{detailInfo.prjName}}</p>
			</div>
		</div>
		<div class="lineBox">
			<div class="lineImg"><img src="../assets/images/icon/level.jpg"></div>
			<p></p>
		</div>
		<div class="doctorBox">
			<div class="doctorImgName">
				<div class="doctorImg"><img src="@/assets/images/example/doctor.png"></div>
				<!--   -->
				<div class="doctorFont">
					<p class="doctorName">{{detailInfo.doctorName}}</p>
					<p class="doctorTitle">{{detailInfo.doctorTitle}}</p>
					<p class="doctorAdd"> | {{detailInfo.hospName}}</p>
				</div>
			</div>
			<div class="doctorHorn"><img src="../assets/images/icon/horn.png"></div>
		</div>
		<div class="lineBox">
			<div class="lineImg"><img src="../assets/images/icon/level.jpg"></div>
			<p></p>
		</div>
		<div class="mation">
			<div class="mationCenter">
				<div v-for="(item,key,index) in listResourceInfo" :key="key">
					<img :src="item.cover || ImgNull" /> 
					<div class="playImg" v-if="item.isVideo==1">
						<img src="@/assets/images/icon/playImg.png" />
					</div>
				</div>
			</div>
			<div class="mationCon">
				<p><span>[{{detailInfo.prjName}}-{{detailInfo.dayIndex}}]</span>{{detailInfo.content}}</p>
			</div>
		</div>			
		<div class="lineBox">
			<div class="lineImg"><img src="../assets/images/icon/level.jpg"></div>
			<p></p>
		</div>
		<div>
			<div class="diaryList">
				<p class="yearFont">2018</p>
				<p class="yearFontA">Beautiful Diary</p>
				<div class="monthBox" v-for="(item,key,index) in listDiaryInfo" :key="key">
					<div class="month">
						<div class="monthImg"><img src="@/assets/images/icon/level.jpg"></div>
						<p>{{item.date10}}</p>
					</div>
					<div class="listContent">
						<div class="contentBox">
							<div v-for="(item2,key,index) in item.listResource" :key="key">
								<img :src="item2.cover || ImgNull">
								<!--     -->
								<div class="playImg" v-if="item2.isVideo==1">
									<img src="@/assets/images/icon/playImg.png" />
								</div>
							</div>
						</div>
						<div class="listFont">
							<!--     -->
							<p><span>[-ST-99]</span>{{item.content}}</p>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div class="bottom_btn">
			<button type="button" name="button" class="btnA"></button>
			<button type="button" name="button" class="btnB"></button>
		</div>
		<div style="height: 3rem;"></div>
	</div>
</template>

<script>
import { Toast } from 'we-vue';
import * as api from '@/assets/js/api';
import * as session from '@/assets/js/session';
export default {
	name: 'diaryDetails',
	data() {
		return {
			detailInfo: '', //
			listResourceInfo: '', //
			listDiaryInfo: '', //
			ImgNull: this.$store.state.ImgNull
		};
	},
	created: function() {
		this.$store.commit('showBottomNav', {
			isShow: false
		});
		api.getdiaryDetails({
			data: {
				openid: this.globalData.openid,
				// id: session.Lstorage.setItem('diaryId')
				id: 1
			}
		}).then(res => {
			if (res.data.flag) {
				console.log('', res.data);
				this.detailInfo = res.data.detail; //
				this.listResourceInfo = res.data.listResource; //
				this.listDiaryInfo = res.data.listDiary; //
			} else {
				Toast.text({
					duration: 1000,
					message: res.data.msg
				});
			}
		});
	}
};
</script>

<style scoped="scoped" lang="scss">
@import '@/assets/css/diaryDetails.scss';
</style>
